
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: Helvetica;
  padding: 30px 100px;
}

@media (max-width: 800px) {
  body {
    padding: 30px 50px;
  }
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  height: 100vh;
  background-color: white;
}

.first-section {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  grid-gap: 30px;
  gap: 30px;
  justify-content: space-evenly;
  width: 100%;
}

.second-section {
  display: flex;
  align-items: center;
  grid-gap: 50px;
  gap: 50px;
  width: 100%;
  margin-top: 40px;
}

.bar {
  width: 70%;
}

.bar, .dough {
  border: 3px solid rgb(254,254,254);
  padding: 50px;
  box-shadow: 0px 6px 10px 0px rgba(33, 30, 67, 0.08);
}

.dough {
  width: 30%;
}

@media (max-width: 800px) {
  .second-section {
    flex-direction: column;
  }

  .bar {
    width: 100%;
  }

  .dough {
    width: 100%;
  }
}
.card {
  border-radius: 10px;
  padding: 15px;
  width: 200px;
}

.card p {
  font-size: 12px;
  margin-bottom: 10px;
}

.card span {
  display: flex;
  align-items: center;
  grid-gap: 10px;
  gap: 10px;
}

.card span:first-child {
  font-size: 50px !important;
}

.card h1 {
  font-weight: 600;
}

